import React, { PureComponent } from 'react';
import Graph from 'react-graph-vis';
import { ServerDetails, ServerNetworkForSelectedServer, ServersNetworkConstants } from './ServersNetworkConstants';
import 'vis-network/styles/vis-network.css';
import './ServersNetworkTile.local.scss';

export default class ServersNetworkTile extends PureComponent {
  events = {
    select: (event) => {
      const { nodes } = event;
      if (nodes.length) {
        const nodeId = nodes[0];
        if (nodeId % 10 === 0) {
          this.props.onServerChanged(nodeId);
        }
      }
    }
  };

  render() {
    const { selectedServer } = this.props;
    this.networkData = selectedServer ? ServerNetworkForSelectedServer : ServersNetworkConstants;
    const serverDetails = ServerDetails;

    return (
      <div className="exec-dashboard-graph-container servers-network-graph-container">
        <Graph
          graph={this.networkData.graph}
          options={ServersNetworkConstants.options}
          events={this.events}
        />
        {selectedServer && (
          <div className="server-details-container">
            {serverDetails.map(row => (
              <div className="server-details-row">{row.name}: {row.value}</div>
            ))}
          </div>
        )}
      </div>
    );
  }
}
